import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import {
  getPaperTypeNo,
  getNationNo,
  getTownNo,
  getDistrictNo,
  getEthinicityNo,
  getEnrollStudyFieldNo,
  GetSchoolNo,
  getQuickLinkComeFromNo,
  getQuickLinkEnrollmentGetAreaNo,
} from 'services/quicklink';
import ComeFromIcon from 'components/icons/ComeFromIcon';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Loại giấy tờ',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__LOAI_GIAY_TO,
    service: getPaperTypeNo,
  },
  {
    title: 'Quốc tịch',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__QUOC_TICH,
    service: getNationNo,
  },
  {
    title: 'Tỉnh',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__TINH,
    service: getTownNo,
  },
  {
    title: 'Huyện',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__HUYEN,
    service: getDistrictNo,
  },
  {
    title: 'Dân tộc',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__DAN_TOC,
    service: getEthinicityNo,
  },
  {
    title: 'Mã ngành tuyển sinh',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__MA_NGANH,
    service: getEnrollStudyFieldNo,
  },
  {
    title: 'Mã trường tuyển sinh',
    key: 'SystemTraining',
    url: routeConstant.DANH_MUC__TUYEN_SINH__MA_TRUONG,
    service: GetSchoolNo,
  },
  {
    title: 'Xuất thân',
    service: getQuickLinkComeFromNo,
    url: routeConstant.DANH_MUC__TUYEN_SINH__XUAT_THAN,
    icon: ComeFromIcon,
  },
  {
    title: 'Khu vực tuyển sinh',
    service: getQuickLinkEnrollmentGetAreaNo,
    url: routeConstant.DANH_MUC__TUYEN_SINH__KHU_VUC,
  },
];

class DashboardEnrollment extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardEnrollment {...props} />
  </Page>
);



